.App * {
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20px;
  border-radius: 15px;
  margin: 1px;
  background-image: url(../Imagenes/fondo-natural-cielo-nubes.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  font-weight: 500;
}

p,
.Valores {
  margin: 3px;
  padding: 10px;
}

p {
  font-weight: 500;
}

h2 {
  margin: 2px;
}

.ClimaPrincipal * {
  background: rgba(0, 0, 0, 0);
}

.ClimaSup {
  border: solid 2px black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.App,
.Temperatura {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Valores,
.Titulo {
  border: solid 2px black;
  background: rgba(255, 255, 255, 0.6);
}

.Datos {
  display: flex;
}

.Temperatura,
.Datos {
  align-items: center;
  justify-content: space-evenly;
}

.DatosSol {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Titulo {
  margin: 10px 10px;
  align-self: stretch;
}

.DatosInf {
  display: grid;
  grid-template-rows: 1fr 4fr;
  justify-content: stretch;
}

.Imagen {
  background: #ffffffe6;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenOscura {
  background: #00000059;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenChicaClara {
  background: #ffffffe6;
  height: 50px;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenGrandeClara {
  background-color: #ffffffe6;
  height: 100px;
  border-radius: 45%;
  border: solid 1px black;
}

.ImagenChicaOscura {
  background: #00000059;
  height: 50px;
  border-radius: 45%;
  border: solid 1px black;
}
.Temperaturas {
  background: rgba(255, 255, 255, 0.45);
  border: solid 2px black;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: stretch;
}

.DatosInferiores {
  border: solid 2px black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.DatosInferiores * {
  margin: 10px 10px;
}
.Colectivo {
  border: solid 1px black;
}

.Simbolo {
  background-image: url(../Imagenes/FonsoSoleado4-transformed.jpeg);
}
